import {
  GraphQLObjectType,
  GraphQLString,
  GraphQLID,
  GraphQLInt,
  GraphQLBoolean,
  GraphQLList,
  GraphQLNonNull
} from 'graphql'

// Get types
import sessionType from '../../management/session_type'
import offerType from '../../service/offer_type'
import institutionType from '../../institution/institution_info_type'
import contactType from '../../institution/contact_type'
import serviceCatType from '../../institution/service_category_type'

// hold all imports
const builtInTypes = {
  GraphQLObjectType,
  GraphQLString,
  GraphQLID,
  GraphQLInt,
  GraphQLBoolean,
  GraphQLList,
  GraphQLNonNull
}

// pass all imports as object to types
const SessionType = sessionType(builtInTypes)
const OfferType = offerType(builtInTypes)
const InstitutionType = institutionType(builtInTypes)
const ContactType = contactType(builtInTypes)
const ServiceCatType = serviceCatType(builtInTypes)

// make available all the types
export {
  SessionType,
  OfferType,
  InstitutionType,
  ContactType,
  ServiceCatType
}



