import {
  GraphQLObjectType,
  GraphQLString,
  GraphQLID,
  GraphQLInt,
  GraphQLBoolean,
  GraphQLList,
  GraphQLNonNull
} from 'graphql'

// Get particular query types
import sessionQuery from '../../management/session_query'
import offerQuery from '../../service/offer_query'
import institutionQuery from '../../institution/institution_info_query'
import contactQuery from '../../institution/contact_query'
import serviceCatQuery from '../../institution/service_category_query'
// Get types
import {
  SessionType,
  OfferType,
  InstitutionType,
  ContactType,
  ServiceCatType
} from '../types'

// hold all imports
const builtInTypes = {
  GraphQLObjectType,
  GraphQLString,
  GraphQLID,
  GraphQLInt,
  GraphQLBoolean,
  GraphQLList,
  GraphQLNonNull
}

// Send custom and built in types to query types
const SessionQuery = sessionQuery(SessionType, builtInTypes)
const OfferQuery = offerQuery(OfferType, builtInTypes)
const InstitutionQuery = institutionQuery(InstitutionType, builtInTypes)
const ContactQuery = contactQuery(ContactType, builtInTypes)
const ServiceCatQuery = serviceCatQuery(ServiceCatType, builtInTypes)

// Make available all the query types a single object

function test() {
  return {
  SessionQuery,
  OfferQuery,
  InstitutionQuery,
  ContactQuery,
  ServiceCatQuery
  }
}
export {
  SessionQuery,
  OfferQuery,
  InstitutionQuery,
  ContactQuery,
  ServiceCatQuery
}
export default test



