import {
  GraphQLSchema,
  GraphQLObjectType
} from 'graphql'

// Get all graphQL Query types
import {
  SessionQuery,
  OfferQuery,
  InstitutionQuery
} from './queries'

// Get all graphQL mutation types
import {
  OfferMutation
} from './mutations'

export default new GraphQLSchema({
  query: new GraphQLObjectType({
    name: 'RootQuery',
    description: 'A root query',
    fields: {
      ...SessionQuery,
      ...OfferQuery,
      ...InstitutionQuery
    }
  }),
  mutation: new GraphQLObjectType({
    name: 'RootMutation',
    description: 'A root mutation',
    fields: {
      ...OfferMutation
    }
  })
})




