import * as test from '../graphql/queries'
console.log(test)

export default function ({ GraphQLObjectType, GraphQLString }) {
  return new GraphQLObjectType({
    name: 'institution_info',
    description: 'Institution Information',
    fields: {
      name: {
        type: GraphQLString,
        description: 'A name of an institution'
      },
      location: {
        type: GraphQLString,
        description: 'Location of an institution'
      },
      // ...ContactQuery,
      // ...ServiceCatQuery
    }
  })
}



